import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Board from '@/views/board'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
	import huodongfenlei from '@/views/modules/huodongfenlei/list'
	import jiaoshi from '@/views/modules/jiaoshi/list'
	import huodongxinxi from '@/views/modules/huodongxinxi/list'
	import syslog from '@/views/modules/syslog/list'
	import forum from '@/views/modules/forum/list'
	import forumtype from '@/views/modules/forumtype/list'
	import yonghu from '@/views/modules/yonghu/list'
	import huodongbaoming from '@/views/modules/huodongbaoming/list'
	import chat from '@/views/modules/chat/list'
	import forumreport from '@/views/modules/forumreport/list'
	import messages from '@/views/modules/messages/list'
	import huodongpingjia from '@/views/modules/huodongpingjia/list'
	import discussgonggaoxinxi from '@/views/modules/discussgonggaoxinxi/list'
	import gonggaoxinxi from '@/views/modules/gonggaoxinxi/list'
	import config from '@/views/modules/config/list'


//2.配置路由   注意：名字
export const routes = [{
	path: '/',
	name: '系统首页',
	component: Index,
	children: [{
		// 这里不设置值，是把main作为默认页面
		path: '/',
		name: '系统首页',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: '修改密码',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '支付',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '个人信息',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/huodongfenlei',
		name: '活动分类',
		component: huodongfenlei
	}
	,{
		path: '/jiaoshi',
		name: '教师',
		component: jiaoshi
	}
	,{
		path: '/huodongxinxi',
		name: '活动信息',
		component: huodongxinxi
	}
	,{
		path: '/syslog',
		name: '系统日志',
		component: syslog
	}
	,{
		path: '/forum',
		name: '活动论坛',
		component: forum
	}
	,{
		path: '/forumtype',
		name: '论坛分类',
		component: forumtype
	}
	,{
		path: '/yonghu',
		name: '用户',
		component: yonghu
	}
	,{
		path: '/huodongbaoming',
		name: '活动报名',
		component: huodongbaoming
	}
	,{
		path: '/chat',
		name: '智能AI',
		component: chat
	}
	,{
		path: '/forumreport',
		name: '举报记录',
		component: forumreport
	}
	,{
		path: '/messages',
		name: '留言板',
		component: messages
	}
	,{
		path: '/huodongpingjia',
		name: '活动评价',
		component: huodongpingjia
	}
	,{
		path: '/discussgonggaoxinxi',
		name: '公告信息评论',
		component: discussgonggaoxinxi
	}
	,{
		path: '/gonggaoxinxi',
		name: '公告信息',
		component: gonggaoxinxi
	}
	,{
		path: '/config',
		name: '轮播图管理',
		component: config
	}
	]
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/board',
		name: 'board',
		component: Board,
		meta: {icon:'', title:'board'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
	mode: 'hash',
	/*hash模式改为history*/
	routes // （缩写）相当于 routes: routes
})
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
